body {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html,
body {
  background-color: #fafafa;
  width: 100vw;
  overflow-x: hidden;
}

body,
html,
input,
button,
select {
  font-size: 17px;
  line-height: 1.47059;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

ul,
ol {
  padding-left: 1em;
}

img {
  max-width: 100%;
  height: auto;
}

* {
  margin: 0;
  padding: 0;
  position: relative;
  text-rendering: optimizeLegibility;
}

.flex {
  display: flex;
}
.flex-wrap {
  display: flex;
}
.flex-direction-column {
  flex-direction: column;
}

.flex-1 {
  flex: 1;
}

.flex-2 {
  flex: 2;
}

.flex-align-center {
  align-items: center;
}

.flex-align-start {
  align-items: flex-start;
}

.flex-align-stretch {
  align-items: stretch;
}

.flex-content-center {
  flex-content: center;
}

.flex-justify-center {
  justify-content: center;
}

