body,
html {
  font-weight: 400;
  font-family: var(--main-font);
  color: #555;
  background-color: #fff;
}

.app {
  max-width: 50rem;
  padding: var(--gap-3) var(--gap-2);
  margin: 0 auto;
}

a {
  color: #2980b9;
}
a:hover {
  color: #3498db;
}

h1,
h2,
h3,
h4 {
  color: #26344a;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.padding-1 {
  padding: var(--gap);
}

button {
  border: 1px solid #00000032;
  background-color: #fcfcfd;
  color: #2c3e50;
  border-radius: 3px;
  font-weight: 500;
  margin: 0;
  padding: 0 var(--gap);
  cursor: pointer;
  touch-action: manipulation;
  height: var(--gap-3);
  line-height: 1;

  user-select: none;

  box-shadow: inset #fff 0 1px 0, #00000006 0 1px 1px, #00000003 0 3px 12px;
}
button:hover {
  background: #f4f4f4;
}
button:active {
  background: #f0f0f0;
  box-shadow: inset #eaeaea 0 1px 0, #00000006 0 1px 1px, #00000003 0 3px 12px;
}

button.-small {
  height: calc(var(--gap) * 2.2);
  font-size: 0.91rem;
}

input[type="text"] {
  background-color: rgba(245, 245, 245, 1);
  border-radius: 3px;
  margin: 0;
  box-shadow: rgba(255, 255, 255, 0.1) 0 1px 0, inset rgba(0, 0, 0, 0.05) 0 1px 0;
  line-height: 1;
  color: #363636;
  cursor: pointer;
  touch-action: manipulation;
  border: 1px solid #00000023;
  padding: 0 var(--gap);
  height: var(--gap-3);
}

input[disabled] {
  color: #666;
  -webkit-text-fill-color: #666;
  opacity: 1;
}

.colorbar-container {
  /*outline: 1px solid #f00;*/
  padding-bottom: var(--gap);
}

.flex-align-center * {
  /*outline: 1px solid #f00;*/
}

.colorbar {
  display: block;
  font-family: Monaco, Menlo, Courier, Courier New, Andale Mono, monospace;
}
.colorbar-element {
  font-size: calc(0.6vmin + 0.5rem);
  text-align: center;
  line-height: 1;
  display: inline-block;
  text-transform: uppercase;
  color: transparent;
  user-select: none;
  cursor: pointer;
  min-width: 4.5em;

  font-weight: 600 !important;
  padding: 0;
  line-height: calc(3.5vmin + 50px);
  width: calc(3.5vmin + 50px);
  height: calc(3.5vmin + 50px);
  border-radius: calc(3.5vmin + 50px);
  /*top: var(--gap-neg);*/
  margin-right: var(--gap-neg);
  margin-bottom: var(--gap-neg);

  box-shadow: rgba(0, 0, 0, 0.05) 0 1px 0, rgba(0, 0, 0, 0.02) 0 3px 16px;

  transition: transform 0.1s, background-color 0.1s, border-radius 0.3s;
  transform: scale(1);
}

.colorbar-element-fragment {
  height: 100%;
  width: 50%;
  top: 0;
  left: 0;
  display: none;
}

.colorbar-element:hover {
  z-index: 100000 !important;
  transform: scale(1.05);
}

.colorbar-element:active {
  z-index: 100000 !important;
  transform: scale(1);
}

.colorbar-element:hover {
  color: #fff;
}
.colorbar-element:active {
}
.colorbar-element:first-child {
}
.colorbar-element:last-child {
}

.input-slider {
  top: 0.1rem;
  width: 12rem;
}
.margin-right-Hx {
  margin-right: 0.5rem;
}

@media screen and (max-width: 820px) {
  h2 {
    font-size: 2.2rem;
  }
}

/* CREDITS */
.credits {
  position: fixed;
  bottom: 20vmin;
  right: 0;
  max-width: 10rem;
  text-align: left;
  font-size: 0.8rem;
}

.credits-header {
  color: #777;
  line-height: 1;
  padding: 0.5rem;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 0.7rem;
  text-align: right;
}

.credits b {
  display: block;
  padding-bottom: 0.25rem;
}

.credits a {
  border-radius: 1px;
  display: block;
  background: rgba(230, 230, 230, 0.25);
  line-height: 1;
  padding: 0.5rem;
  color: #444;
  margin: 1px;
  text-decoration: none;
  box-shadow: inset rgba(255, 255, 255, 0.1) 0 1px 0, rgba(0, 0, 0, 0.02) 0 1px 0;
}
.credits a:hover {
  background: #fff;
}
@media screen and (max-width: 1200px) {
  .credits {
    position: relative;
    max-width: none;
    display: flex;
    margin: 0 auto;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 1rem;
    bottom: 0;
    right: 0;
    margin-bottom: 1rem;
  }
  .credits-header {
    width: 5rem;
    padding-top: 1rem;
  }
  .credits a {
    width: 8rem;
  }
}

#carbonads {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    "Helvetica Neue", Helvetica, Arial, sans-serif;
}

#carbonads {
  display: flex;
  max-width: 330px;
  background-color: hsl(0, 0%, 98%);
  box-shadow: 0 1px 4px 1px hsla(0, 0%, 0%, 0.1);
}

#carbonads a {
  color: inherit;
  text-decoration: none;
}

#carbonads a:hover {
  color: inherit;
}

#carbonads span {
  position: relative;
  display: block;
  overflow: hidden;
}

#carbonads .carbon-wrap {
  display: flex;
}

.carbon-img {
  display: block;
  margin: 0;
  line-height: 1;
}

.carbon-img img {
  display: block;
}

.carbon-text {
  font-size: 13px;
  padding: 10px;
  line-height: 1.5;
  text-align: left;
}

.carbon-poweredby {
  display: block;
  padding: 8px 10px;
  background: repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 5px,
      hsla(0, 0%, 0%, 0.025) 5px,
      hsla(0, 0%, 0%, 0.025) 10px
    )
    hsla(203, 11%, 95%, 0.4);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-weight: 600;
  font-size: 9px;
  line-height: 1;
}

#carbonads {
  position: absolute;
  left: 0;
  bottom: 20vmin;
}

@media screen and (max-width: 1350px) {
  #carbonads {
    position: relative;
    bottom: 0;
    margin: 0 auto 2rem;
  }
}

