:root {
  --main-font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", sans-serif;
  --header-font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", sans-serif, sans-serif;

  --gap: 14px;
  --gap-2: 28px;
  --gap-3: 42px;
  --gap-neg: -14px;
  --gap-neg-2: -28px;
}

body,
button {
  font-size: 16px;
  line-height: 24px;
}

.padding-vertical-1 {
  padding-top: var(--gap);
  padding-bottom: var(--gap);
}

.text {
  font-size: 16px;
  line-height: 24px;
  padding: 0.05px 0;
}

.text::before {
  content: "";
  margin-top: -0.44208984375em;
  display: block;
  height: 0;
}

.text::after {
  content: "";
  margin-bottom: -0.39765625em;
  display: block;
  height: 0;
}

h3 {
  font-size: 22px;
  line-height: 27px;
  padding: 0.05px 0;
}

h3::before {
  content: "";
  margin-top: -0.3048739346590909em;
  display: block;
  height: 0;
}

h3::after {
  content: "";
  margin-bottom: -0.2604403409090909em;
  display: block;
  height: 0;
}

h2 {
  font-size: 28px;
  line-height: 33px;
  padding: 0.05px 0;
}

h2::before {
  content: "";
  margin-top: -0.2800362723214286em;
  display: block;
  height: 0;
}

h2::after {
  content: "";
  margin-bottom: -0.23560267857142855em;
  display: block;
  height: 0;
}

h1 {
  font-size: 44px;
  line-height: 50px;
  padding: 0.05px 0;
}

h1::before {
  content: "";
  margin-top: -0.25828302556818183em;
  display: block;
  height: 0;
}

h1::after {
  content: "";
  margin-bottom: -0.2138494318181818em;
  display: block;
  height: 0;
}

/*h1::after,
h2::after,
h3::after,
h4::after,*/
.text.-gap::after {
  padding-bottom: var(--gap);
}

h1,
h2,
h3,
h4,
.text {
  display: block;
  font-family: var(--header-font);
  font-weight: 400;
}
button,
input,
textarea {
  font-family: var(--main-font);
  font-weight: 400;
}

h1 {
  letter-spacing: -0.3px;
}
h2 {
  letter-spacing: -0.15px;
}
h3 {
  letter-spacing: -0.1px;
}
h1 strong,
h2 strong,
h3 strong {
  font-weight: 700;
}
